




























import { ref } from '@vue/composition-api'
import Vue from 'vue';
import Home from './views/Home.vue'

export default Vue.extend({
  name: 'App',

  components: {
    Home,
  },

  setup() {
    if(!window.location.href.includes('www')) window.location.href = 'https://nejj.me/projects/disrupt-tracker'
    const loggedRoutes = ref(['Home', 'Statistics', 'Settings'])
    return { loggedRoutes }
  }
});
